body {
  display: flex;
  justify-content: center;
  padding-top: 2rem;
  font-family: Arial, Helvetica, sans-serif;
}

.container {
  width: 20rem;
  padding: 2rem;
  border: 1px solid;
  border-radius: 20px 5px;
}

.flex {
  display: flex;
  justify-content: space-between;
  gap: 1rem;
}

h1 {
  text-align: center;
}

input {
  height: 1rem;
}

.add-button {
  text-align: center;
  margin-top: 1rem;
}
.add-button button {
  font-size: 1em;
}

.center {
  text-align: center;
}

button {
  background-color: aliceblue;
  border-radius: 10px;
  padding: 0.5em 2em;
}
button:hover {
  background-color: #cdd5db;
}

.notify {
  width: 100%;
  border: 2px solid;
  border-radius: 2px;
  background-color: #d4d4d4;
  padding: 0.5rem 1rem;
  box-sizing: border-box;
}

.success {
  color: green;
  border-color: green;
}

.fail {
  color: red;
  border-color: red;
}
